* {
  margin: 0;
  background-color: rgb(31, 30, 30);
  color: white;
}

body {
  margin: 0;
  font-family: 'Roboto Slab', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.login {
    display: flex;
    background-color: rgb(56, 55, 55);
}

.img-background {
    max-width: 70vw;
    width: 70vw;
    height: 100vh;
    display: flex;
}
.button {
    transition-duration: 0.4s;
}

.buttonGreen {
    background-color: white;
    color: black;
    border: 2px solid #4CAF50;
}

.buttonGreen:hover {
    background-color: #4CAF50;
    color: white;
}

.buttonBlue {
    background-color: rgba(255, 255, 255, 0);
    color: rgb(255, 255, 255);
    border: 2px solid #008CBA;
}

.buttonBlue:hover {
    background-color: #008CBA;
    color: white;
}

.buttonRed {
    background-color: white;
    color: black;
    border: 2px solid #f44336;
}

.buttonRed:hover {
    background-color: #f44336;
    color: white;
}

.buttonWhite {
    background-color: white;
    color: black;
    border: 2px solid #e7e7e7;
}

.buttonWhite:hover {
    background-color: #e7e7e7;
    color: white;
}

.buttonWhite2 {
    background-color: white;
    color: black;
    border: 2px solid #555555;
}

.buttonWhite2:hover {
    background-color: #555555;
    color: white;
}
input[type=email] {
  background: transparent;
  outline-width: 0;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: none;
  font-size: 15px;
  margin-top: 10px;
  margin-bottom: 10px;
}

#validation-email {
  /* margin-bottom: 0.1vw; */
  margin-top: 0.2vw;
  /* height: 0.01vw; */
  font-size: 0.6rem;
  color: rgb(255, 0, 0);
  background-color: transparent;
  text-align: left;
  width: 250px;
}
input[type=text] {
  background: transparent;
  outline-width: 0;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: none;
  font-size: 15px;
  margin-top: 10px;
  margin-bottom: 10px;
}

input[type=password] {
  background: transparent;
  outline-width: 0;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: none;
  font-size: 15px;
  margin-top: 10px;
  margin-bottom: 10px;
}

#validation-pass {
  margin-top: 0.2vw;
  font-size: 0.6rem;
  color: rgb(255, 0, 0);
  background-color: transparent;
  text-align: left;
  width: 250px;
}
.field-login {
    width: 30vw;
    height: 100vh;
    max-width: 30vw;
    min-width: 300px;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: black;
}

.field-login li {
    background-color: black;
    margin-top: 15px;
    cursor: pointer;
    font-size: 0.7rem;
    list-style: none;
}

#link-recover,
#link-recover a,
#link-register,
#link-register a {
    text-decoration: none;
    background-color: transparent;
}

#link-recover a:hover,
#link-register a:hover {
    color: #008CBA;
}

.title-login {
    margin-top: 12vw;
    font-size: 200%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background: -webkit-linear-gradient(#008CBA, rgb(41, 41, 41));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.area-login {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: black;
}

.input-email {
    margin-top: 1.4vw;
    width: 250px;
    background-color: rgb(24, 24, 24);
    /*teste*/
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-radius: 10px;
    border-bottom: #008CBA 1px;
}

.input-password {
    margin-top: 1vw;
    width: 250px;
    background-color: rgb(24, 24, 24);
    /*teste*/
    display: flex;
    text-align: center;
    justify-content: flex-start;
    align-items: center;
    border-radius: 10px;
}

.input-email svg {
    margin-left: 5px;
    margin-right: 5px;
}

.input-password svg {
    margin-left: 5px;
    margin-right: 5px;
}

h5 {
    width: 15vw;
    text-align: left;
    margin-left: 7vw;
}

.button {
    margin-top: 0.4vw;
    width: 250px;
    height: 34px;
    font-size: large;
    border-radius: 10px;
    border: none;
    background-color: rgb(24, 24, 24);
    margin-bottom: 1px;
}

.area-button {
    background-color: black;
    margin-top: 1vw;
    width: auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.textarea:hover,
.input-email:hover,
.input-password:hover,
.button:hover {
    box-shadow: 0px 0px 2px 2px #008CBA;
}

.login-eye {
    align-items: center;
    justify-content: center;
    font-size: 30;
    cursor: pointer;
    margin-right: 5px;
    background-color: transparent;
}

.botton-login {
    display: flex;
    background-color: transparent;
    justify-content: space-between;
    width: 250px;

}
