.button {
    transition-duration: 0.4s;
}

.buttonGreen {
    background-color: white;
    color: black;
    border: 2px solid #4CAF50;
}

.buttonGreen:hover {
    background-color: #4CAF50;
    color: white;
}

.buttonBlue {
    background-color: rgba(255, 255, 255, 0);
    color: rgb(255, 255, 255);
    border: 2px solid #008CBA;
}

.buttonBlue:hover {
    background-color: #008CBA;
    color: white;
}

